@import '../partials/variables';
@import 'theme1';
@import 'theme2';

.atw_wrapper {

    //Tabs
    .tab-content .tab-pane {display: block;height:0}
    .tab-content .active {height:auto}
    .fade {
        transition: opacity 0.15s linear;
    }

    @media screen and (prefers-reduced-motion: reduce) {
        .fade {
            transition: none;
        }
    }
    .fade:not(.show) {
        opacity: 0;
    }

    //Buttons
    .atw_btn{
        line-height: 35px;
        padding: 0 11px;
        background: $theme1;
        color: #fff;
        text-transform: uppercase;
        font-size: 12px;
        font-weight: 500;
        display: inline-flex;
        align-items: center;
        transition: $transition;
        &.atw_btn--light{
            border: 1px solid $borderColor;
            background: none;
            color: #9192a3;
            &:hover{
                border-color: $theme1;
                background: $theme1;
                color: #fff;
            }
        }
    }
    .atw_btn--icon{
        span{
            padding-right: 10px;
            display: inline-block;
            font-size: 16px;
        }
    }
    .atw_btn--radius{
        border-radius: 3px;
    }
    .atw_btn--rounded{
        border-radius: 25px !important;
    }

    //Cards
    .atw_card{
        border: 1px solid $borderColor;
        &.atw_card_rounded{
            border-radius: 3px;
        }
    }

    //Badges
    .atw_badge--default{
        border-radius: 3px;
        padding: 0 12px;
        line-height: 26px;
        font-size: 12px;
        font-weight: 500;
        color: #fff;
        display: inline-block;
        text-transform: uppercase;
        &.atw_badge--left{
            background: $theme1;
        }
        &.atw_badge--right{
            background: #00b46a;
        }
        &.atw_badge--blue{
            background: $theme2;
        }
        &.atw_badge--straight{
            border-radius: 0;
        }
    }
    .atw_badge--rounded{
        border-radius: 25px;
    }
    .atw_floated_badge{
        position: absolute;
        &.badge--left{
            left: 10px;
            top: 10px;
        }
        &.badge--right{
            right: 10px;
            top: 10px;
        }
    }
    .float-d-ratio{
        border: 1px solid $theme1;
        font-size: 12px;
        color: #fff;
        line-height: 26px;
        padding: 0 10px;
        border-radius: 25px;
        margin-left: 4px;
        display: block;
        background: $theme1;
    }
    //BG
    &.bgColor{
        background: #eff0f2;
        padding: 100px 0;
        margin-top: 80px;
        margin-bottom: 80px;
    }


    //General Styles
    .atw_product_actions2{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        justify-content: center;
        transition: $transition;
        li{
            margin-right: 5px;
            &:last-child{
                margin-right: 0;
            }
            a.atw_btn{
                background: $theme2;
                padding: 0 20px;
                line-height: 40px;
            }
            &:first-child, &:last-child{
                a{
                    display: block;
                    height: 40px;
                    width: 40px;
                    border: 1px solid $borderColor;
                    border-radius: 50%;
                    text-align: center;
                    padding-top: 12px;
                    color: #9192a3;
                    transition: $transition;
                    &:hover{
                        border-color: $theme2;
                        color: #fff;
                        background: $theme2;
                    }
                }
            }
        }
    }
    .atw_item_header{
        padding: 15px 15px 0 15px;
        .atw_item_cat{
            font-size: 12px;
            color: #9192a3;
            display: block;
            margin-bottom: 5px;
        }
        .atw_item_title{
            font-weight: 400;
            a{
                color: #363940;
                font-size: 16px;
                line-height: 26px;
                transition: $transition;
                &:hover{
                    color: $theme2;
                }
            }
        }
    }
    .atw_item_info2{
        display: flex;
        justify-content: space-between;
        align-items: center;
        .atw_price{
            color: $theme2;
            del{
                color: #9192a3;
                font-weight: 300;
                font-size: 14px;
                display: inline-block;
                padding-left: 4px;
            }
        }
    }

    .atw_title{
        text-align: center;
        padding-bottom: 22px;
        position: relative;
        margin-bottom: 40px;
        &:before{
            position: absolute;
            height: 2px;
            width: 50px;
            background: #e4e8ed;
            content: '';
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
        }
        h3{
            color: #202428;
            font-size: 24px;
            font-weight: 400;
        }
    }

    .atw_item{
        .atw_item_top{
            position: relative;
            .atw_overlay_content, .atw_badge{
                position: absolute;
            }
            .atw_overlay_content{
                transition: 0.3s;
            }
            .atw_product_actions{
                li{
                    display: inline-block;
                }
            }
        }
        .atw_item_bottom{
            .atw_item_info{
                .atw_rating{
                    display: inline-flex;
                    align-items: center;
                    .la{
                        font-size: 16px;
                        margin-right: 0;
                        &.la-star, &.la-star-half-o{
                            color:#feb507;
                        }
                        &.la-star-o{
                            color: #c6c6d3;
                        }
                    }
                    .total-rating{
                        font-size: 12px;
                        color: #9192a3;
                        display: inline-block;
                        padding-left: 7px;
                    }
                }
            }
        }

    }
    .slider_control{
        line-height: 38px;
        width: 40px;
        text-align: center;
        z-index: 2;
        display: inline-block;
        font-size: 12px;
        background: #fff;
        cursor: pointer;
        &.slider_control_round{
            border-radius: 50%;
        }
    }

}

//Grid Styles
.atw_wrapper{
    .atw_grid_wrapper{
        .atw_item{
            margin-bottom: 30px;
        }
        img{
            width: 100%;
        }
        &.atw_slider_wrapper{
            img{
                width: 100%;
            }
        }
    }
    &.atw_grid{
        #atw_style8 .atw_tabs .nav{
            margin-bottom: 25px;
        }
    }

    //Style 14
    .m-0{
        padding-left: 0;
        padding-right: 0;
        margin-right: -1px;
        margin-top: -1px;
        flex-grow: 1;
        .atw_item{
            margin-bottom: 0;
        }
        &:last-child{
            flex-grow: 0;
        }
    }
}













