/*****************************
	RESPONSIVE
*****************************/
@import "variables";
/*this is responsive scss*/
$tablet: 768px;
$laptop: 1070px;
$ipad: 992px;

/* 992 TO 1199 */
@media (min-width:$ipad ) and (max-width: 1199px) {
    #atw_style5 .atw--single_item .atw_item_top .atw_overlay_content{
        left: 20px;
        width: calc(100% - 40px);
    }
    .atw_wrapper .m-0:last-child{
        flex-grow: 1;
    }
}

/* 991 */
@media (max-width: $ipad - 1) {
    .atw_wrapper .m-0:last-child{
        flex-grow: 1;
    }
}

/* 768 TO 992*/
@media (min-width: $tablet) and (max-width: $ipad - 1px) {

}

/* 767 */
@media  screen and (max-width: $tablet - 1) {

}

/* 320 TO 575 */
@media  (min-width: 320px) and (max-width: 480px) {
    .atw_wrapper .m-0{
        flex:1 100%;
        padding-left: 15px;
        padding-right: 15px;
    }
}